import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

export default{
	mixins: [common,globals],
	data () {
		return {
			data: []
		}
	},
	methods: {
		redirect (row) {
			let path = './comment/'+row.catt_id
			if(row.review){
				path = '/reviews/'+row.review.rvw_id
			}
			this.$router.push({path:path})
		},
		reviewObject (row) {
			let result = '点评对象'
			
			if(row.lesson_type===0){					
				if(!_.isEmpty(row.cls)){
					result = row.cls.class_name
				}				
				if(!row.cls&&row.course_arrange){
					result = row.course_arrange.name
				}
			}
			if(row.lesson_type===1){
				result = '一对一'
			}
			if(row.lesson_type===2){
				result = '一对多'
			}
			
			return result
		},
		notTrial (row) {
			let result = true
			
			if(row.course_arrange&&row.course_arrange.is_trial===1){
				return false
			}
			
			return result
		}
	},
	computed: {
		data_uri () {
			return 'class_attendances?eid='+this.eid$+'&with=cls,course_arrange'
		}
	}
}




